@import '../../../settings/settings';
@import '../../../components/inputs';
@import '../../../icons/iconfont-varible';
@import '../../../icons/iconfont-icons';

* {
	margin: 0;
	padding: 0;
}

body {
	font-family: $font-stack;
	letter-spacing: 0.02em;
	font-weight: 400;
	color: #FFF;
}

ul {
	list-style: none;
}

button {
	border: none;
	outline: none;
}

.logo {
	@include block-create(71px, 66px, url('../../../../img/logo.svg'));
	@media (max-width: $mobile-size) {
		margin: 0 auto;
	}
}

.container {
	position: relative;
	max-width: 960px;
	margin: 0 auto;
	overflow: hidden;
}

.footer {
	padding: 70px 0 30px 0;
	background: $black-color url('../../../../templates/footers/views/footer7/img/footer-bg.jpg');
	background-size: cover;
	&__copyright {
		@include pos('bottomRight', 0, 10px);
		@include text-config(14px, 36px, rgba(255,255,255,.5));
	}
	&__part {
		margin-bottom: 20px;
		padding: 10px;
		float: left;
		&--logo {
			width: 16%;
			padding-top: 15px;
			@media (max-width: $tablet-size) {
				width: 25%;
			}
			@media (max-width: $mobile-size) {
				width: 100%;
			}
		}
		&--list {
			width: 16%;
			&-link {
				@include text-config(16px, 32px, rgba(255,255,255,.5));
				position: relative;
				font-weight: 600;
				text-decoration: none;
				&:hover {
					color: rgba(255,255,255,1);
				}
				&.is-new > span {
					@extend %border-radius;
					@include pos('topLeft', 50%, 100%);
					@include text-config(14px, 28px, $black-color);
					margin-left: 10px;
					padding: 0 10px;
					background-color: $orange-color;
					text-transform: uppercase;
					-webkit-transform: translateY(-50%);
					transform: translateY(-50%);
				}
			}
			&:nth-child(4) {
				@media (max-width: $mobile-size) {
					float: right;
				}
			}
			@media (max-width: $tablet-size) {
				width: 25%;
			}
			@media (max-width: $mobile-size) {
				width: 50%;
				text-align: left;
			}
		}
		&--info {
			width: 34%;
			@media (max-width: $tablet-size) {
				width: 100%;
			}
		}
	}
	@media (max-width: $mobile-size) {
		padding: 30px 0;
	}
}

.list-title,
.follow__title,
.subscribe__title {
	font-size: 16px;
	line-height: 36px;
	text-transform: uppercase;
}

.social-nav {
	margin-top: 15px;
}
.icon {
	@extend %border-radius;
	display: inline-block;
	vertical-align: middle;
	width: 36px;
	height: 36px;
	margin-right: 8px;
	text-align: center;
	font-size: 14px;
	line-height: 36px;
	color: #FFF;
	text-decoration: none;
	-webkit-transition: 0.3s;
	transition: 0.3s;
	&:last-child {
		margin-right: 0;
	}
	&--facebook {
		background: $dark-blue-color;
		&:hover {
			background-color: #295185;
		}
	}
	&--twitter {
		background: $twitter-color;
		&:hover {
			background-color: #1D86B4;
		}
	}
	&--dribbble {
		background: $dribbble-color;
		&:hover {
			background-color: #BE428E;
		}
	}
	&--vimeo {
		background: $vimeo-color;
		&:hover {
			background-color: #2C7A93;
		}
	}
	&--google-plus {
		background: $google-plus-color;
		&:hover {
			background-color: #C3504D;
		}
	}
}


.follow-box,
.subscribe-box {
	margin-bottom: 40px;
}

.subscribe-box {
	@media (max-width: $tablet-size) {
		width: 60%;
	}
	@media (max-width: $mobile-size) {
		width: 100%;
	}
}

.subscribe {
	&__text {
		@include text-config(16px, 20px, rgba(255,255,255,.5));
		margin-bottom: 10px;
		font-weight: 600;
	}
	&__input-group {
		position: relative;
	}
	&__field {
		background: rgba(255,255,255,.2);
		border: none;
		color: #FFF;
		&:valid {
			width: calc(100% - 46px);
		}
		&:valid + .subscribe__btn {
			border-top-left-radius: 6px;
			border-bottom-left-radius: 6px;
			background-color: rgba(36,170,152,1);
			cursor: pointer;
		}
	}
	&__btn {
		position: absolute;
		top: 0;
		right: 0;
		width: 36px;
		height: 36px;
		border-top-right-radius: 6px;
		border-bottom-right-radius: 6px;
		background-color: rgba(36,170,152,.5);
		color: #FFF;
		&:after {
			content: "";
			position: absolute;
			top: 50%;
			left: calc(50% + 3px);
			width: 2px;
			height: 2px;
			background-color: currentColor;
			-webkit-transform: translate(-50%, -50%);
			transform: translate(-50%, -50%);
			box-shadow: -1px -1px 0 0 currentColor,
			-2px -2px 0 0 currentColor,
			-3px -3px 0 0 currentColor,
			-4px -4px 0 0 currentColor,
			-1px 1px 0 0 currentColor,
			-2px 2px 0 0 currentColor,
			-3px 3px 0 0 currentColor,
			-4px 4px 0 0 currentColor;
		}
	}
}